.landing-page {
  padding: 30px 0;
  color: var(--primary);
}
.landing-page hr {
  opacity: 0.2;
  margin: 80px 0;
}

.mt-section {
  margin-top: 40px;
}
.mb-section {
  margin-bottom: 20px;
}

.landing-logo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.landing-logo img {
  border: 3px solid;
  border-radius: 25px;
  box-shadow: 1px 1px 3px 1px var(--fg);
}

.landing-logo h2 {
  margin: 15px 0 0;
  font-size: 30pt;
  font-weight: bold;
}
.landing-logo h3 {
  margin: 15px 0 0;
  font-size: 24pt;
}

.landing-logo p, .landing-logo a {
  font-size: 12pt;
  text-align: center;
  max-width: 550px;
  color: var(--fg);
  margin: 5px 0;
  text-decoration: none;
}

.webstore-download {
  margin: 20px auto 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: min-content;
  color: var(--fg);
  border: 1px solid var(--fg);
  font-size: 18pt;
  white-space: nowrap;
  padding: 0 10px;
  border-radius: 7px;
  cursor: pointer;
}
.webstore-download:hover {
  background-color: #fff3;
}
.webstore-download img {
  width: 80px;
}

.carousel-container {
  margin-top: 20px;
}
.carousel-text {
  text-align: center;
  color: var(--fg);
  min-height: 100px;
  max-width: 600px;
  margin: auto;
}
.carousel-item {
  padding: 10px;
}
.carousel-item img {
  width: 100%;
  box-shadow: 0px 0px 10px 3px var(--primary);
}

.video-container video {
  width: 100%;
}

.user-comments {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 80px;
}
.user-comment {
  position: relative;
  color: var(--fg);
  font-size: 16pt;
  max-width: 600px;
  text-align: center;
}
.avatar-container {
  position: absolute;
  right: 0;
  bottom: -30px;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  box-shadow: 0 0 2px 1px var(--fg)
}
.avatar-container img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
