
:root {
  --bg: #0a0a0a;
  --bg-opacity: #0a0a0aA0;
  --fg: #ccc;
  --primary: rgb(66, 133, 245);
}

* {
  transition: all .3s;
  box-sizing: border-box;
  user-select: none;
}
*::-webkit-scrollbar {
  width: 8px;
  height: 0;
}
*::-webkit-scrollbar-track {
  background: #0003; 
}
*::-webkit-scrollbar-thumb {
  background: #888; 
}
*::-webkit-scrollbar-thumb:hover {
  background: #555; 
}

html, body, #root, #app, #app>div {
  height: 100%;
  padding: 0px;
  margin: 0px;
}

body {
  margin: 0;
  width: 100%;
  height: 100%;
  min-height: 100%;
  font-family: 'Roboto';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#root>div {
  min-height: 100%;
  background-color: var(--bg);
}

.d-flex {
  display: flex;
}
.flex-wrap {
  flex-wrap: wrap;
}
.justify-content {
  justify-content: center;
}
.justify-content-between {
  justify-content: space-between;
}
.justify-content-center {
  justify-content: center;
}
.justify-content-end {
  justify-content: flex-end;
}
.align-items-center {
  align-items: center;
}

.mr-auto {
  margin-right: auto;
}

.main-content {
  max-width: 900px;
  margin: auto;
}

.menu {
  position: sticky;
  top: 0;
  z-index: 100;
  padding: 20px;
  background-color: var(--bg);
}

.right-menu {
  display: flex;
  align-items: center;
}

.menu-item {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  margin-left: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  position: relative;
}
.menu-item::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  border-bottom: 2px solid var(--bg);
}
.menu-item.active::after {
  border-color: var(--fg);
}

.menu-item:hover svg {
  color: var(--fg);
}

.menu-item svg {
  width: 50px;
  height: 50px;
  color: rgb(200, 200, 200);
}
.menu-item.active svg {
  color: var(--fg);
  width: 55px;
  height: 55px;
}

.shop-item {
  width: 33%;
  padding: 20px;
  max-width: 300px;
  cursor: pointer;
}

.product-img {
  position: relative;
  min-height: 100px;
  max-height: 250px;
  height: 30vw;
  border: 2px solid var(--primary);
  overflow: hidden;
  border-radius: 10px;
}

.product-img:hover img {
  transform: scale(1.1);
}

.shop-item.shared .product-img, .shop-item.purchased .product-img {
  border-width: 15px;
  border-radius: 15px;
}
.shop-item.purchased .product-img {
  border-color: rgb(47 183 89);
}
.shop-item.shared .product-img {
  border-color: var(--primary);
}
.shop-item.shared.purchased .product-img {
  background: linear-gradient(var(--bg), var(--bg)) padding-box, linear-gradient(to bottom, rgb(47 183 89), var(--primary)) border-box;
  border-color: transparent;
}

.item-name, .item-price {
  color: var(--primary);
  font-weight: bold;
  font-size: 15px;
  margin: 4px 10px 0 0;
}
.item-price {
  color: rgb(47 183 89);
}

.insert-link svg, .close-link svg {
  color: rgb(150, 150, 150);
}

.insert-link svg {
  transform: rotate(-45deg);  
}

.insert-link:hover svg, .close-link:hover svg {
  opacity: 0.7;
}

.order-status-badge {
  position: absolute;
  right: -50px;
  top: 30px;
  width: 200px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--fg);
  background-color: #260101;
  box-shadow: 0px 0px 5px 1px var(--bg);
  transform: rotate(45deg);
}
.order-status-badge.color-placed {
  background-color: #ff4500;
}
.order-status-badge.color-shipped {
  background-color: rgb(47 183 89);
}

.item-cart, .item-user {
  background-color: rgb(200, 200, 200);
  border-radius: 5px;
  padding: 5px;
  margin-left: 5px;
  cursor: pointer;
}

.more-btn {
  background-color: var(--bg) !important;
  color: var(--primary) !important;
  font-size: 30px !important;
  font-weight: bold !;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  outline: none !important;
}
.more-btn svg {
  width: 40px;
  height: 40px;
}

.sub-menu {
  padding-right: 10px;
}

.total-price {
  color: var(--primary);
  margin-right: 10px;
  font-size: 30px;
  font-weight: bold;
  display: flex;
  align-items: center;
}

.checkout-btn {
  display: flex;
  align-items: center;
}

.checkout-btn svg {
  height: 40px;
  width: 40px;
}

.card-container {
  position: relative;
}

.link {
  position: absolute;
  top: 15px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: var(--primary)70;
  justify-content: center;
  align-items: center;
  display: none;
}

.link.left {
  left: 15px;
}
.link.right {
  right: 15px;
}

.card-container:hover .link, .selected .link.left {
  display: flex;
}

.link:hover {
  background-color: var(--primary)50;
}
.selected .link {
  background-color: var(--primary)A0;  
}

.link svg {
  color: gray;
  width: 40px;
  height: 40px;
}

.sign-in {
  padding-top: 70px;
  max-width: 400px;
  margin: auto;
}

.input-label {
  color:var(--primary);
  font-size: 25px;
  font-weight: bold;
  text-align: center;
  margin: 10px 0;
}
.input-label.small {
  font-size: 18px;
}
.input-description {
  color:var(--fg);
  font-size: 15px;
  text-align: center;
  margin: 10px 0;
  padding: 10px;
  border: 1px solid var(--fg);
  border-radius: 10px;
  user-select: none;
}
.input-description a {
  color: var(--fg);
}

.input-text input {
  height: 50px;
  width: 100%;
  border: 2px solid var(--primary);
  border-radius: 20px;
  background-color: transparent;
  font-size: 25px;
  font-weight: bold;
  color: var(--fg);
  text-align: center;
}

.input-button {
  width: 100%;
  background-color: var(--primary);
  border-radius: 20px;
  color: var(--fg);
  font-size: 25px;
  font-weight: bold;
  height: 50px;
  margin-top: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.input-button:hover {
  opacity: 0.8;
}
.input-button:active {
  opacity: 0.6;
}

.error-msg {
  background-color: rgb(218, 134, 8);
  color: var(--fg);
  font-size: 15px;
  padding: 0 20px;
  border-radius: 15px;
  margin-top: 20px;
}

.email-header {
  font-size: 30px;
  font-weight: bold;
  color: var(--primary);
  display: flex;
  align-items: center;
}

.profile-page {
  max-width: 400px;
  padding-top: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
}

.avatar {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  border: 2px solid var(--primary);
  overflow: hidden;
  cursor: pointer;

  display: flex;
  align-items: center;
  justify-content: center;
}
.avatar.selected {
  border-width: 7px;
}

.avatar img {
  width: 180px;
  height: 180px;
  border-radius: 50%;
}

.share-email {
  color: var(--primary);
  font-size: 25px;
  font-weight: bold;
  display: flex;
  align-items: center;
}

.shared-header {
  display: flex;
  justify-content: center;
  color: var(--primary);
  font-size: 25px;
  font-weight: bold;
}

.shared-url {
  text-decoration: underline;
}
.shared-url svg {
  margin-left: 15px;
  color: rgb(150, 150, 150);
  cursor: pointer;
}
.shared-url svg:hover {
  color: rgb(200, 200, 200);
}

.no-card-container {
  padding-top: 50px;
  max-width: 400px;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.no-card-container img {
  width: 200px;
}

.no-card-text {
  margin-top: 20px;
  font-size: 30px;
  font-weight: bold;
  color: var(--primary);
  text-align: center;
}

.privacy-iframe {
  width: 100%;
  height: calc(100vh - 120px);
  border: none;
}

.loading-container {
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  background-color: #00000080;
  backdrop-filter: blur(4px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10000;
}
.verify-container {
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  background-color: #00000080;
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--primary);
  font-size: 20pt;
}
.loading-spinner {
  width: 80px !important;
  height: 80px !important;
  color: var(--primary) !important;
}

.purchase-button {
  max-width: 400px;
  margin: auto;
}

.ollacart-modal {
  display: flex;
  justify-content: center;
  align-items: center;
  backdrop-filter: blur(5px);
}
.ollacart-modal>div:first {
  background-color: var(--bg-opacity);
}

@media screen and (max-width: 768px) {
  .menu {
    padding-left: 0;
    padding-right: 0;
  }
  .shop-item {
    width: 50%;
  }
  .product-img {
    height: 50vw;
  }
  .shared-header {
    font-size: 15px;
  }
  .share-email {
    font-size: 15px;
  }
  .shared-url svg {
    width: 15px;
    height: 15px;
    margin-left: 5px;
  }
}
@media screen and (max-width: 500px) {
  .shop-item {
    width: 100%;
  }
  .menu-item {
    margin-left: 0;
  }
  .product-img {
    height: 50vw;
  }
}
@media screen and (max-width: 390px) {
  .shared-header {
    font-size: 13px;
  }
  .share-email {
    font-size: 13px;
  }
}